.container {
  flex-direction: row;
  display: flex;
  max-height: 100vh;
  height: 100vh;
  overflow: hidden;
  background-color: #fff;
}

.container-modal {
  padding: 20px;
}

.container-logo-form {
  width: 60%;
  display: flex;
  flex-direction: column;
}

.container-form-login {
  background-color: #fff;
  display: flex;
  justify-content: center;
  padding-top: 100px;
}

.paddingTop0 {
  padding-top: 0 !important;
}

.container-form-two {
  align-self: center;
}

.line {
  border-color: #ffffffa1;
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  border-top-width: 1px;
}

.container-image {
  width: 40%;
  background-color: #2563eb;
}

.text-light {
  margin-top: 3rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
  color: #6b7280;
}

.container-forgot-password {
  text-align: right;
  margin: 0.7rem 0;
  margin-bottom: 1.3rem;
}

.from-msm {
  background-color: #e9b3b3;
  height: 20px;
  border-radius: 5px;
  text-align: center;
  padding: 10px;
  position: absolute;
  bottom: 20px;
  left: 20px;
}
select.select-nivel {
  padding: 16px 0 16px 16px;
    width: 100%;
    border-radius: 0.5rem;
    background-color: #e5e7eb;
    border-width: 1px;
    border-color: #e5e7eb;
    border-style: solid;
    margin-bottom: 0.2rem;
    margin-top: 0.6rem;
    font-family: "InterLight";;
}

@media only screen and (max-width: 700px) {
  .container {
    overflow: auto;
  }
  .container-image {
    display: none;
  }

  .container-logo-form {
    width: 100%;
  }

  .container-form {
    padding-top: 0;
    padding: 0 14px;
  }
}
